<template>
  <div class="rank-container">
    <div class="rank-header">
      <h2>üèÜ ÂØªÂÆùÊéíË°åÊ¶ú</h2>
      <button @click="backToGame" class="back-btn">ËøîÂõûÊ∏∏Êàè</button>
    </div>
    
    <div class="rank-list">
      <!-- Á¨¨‰∏ÄÂêç -->
      <div class="rank-item top-rank first-place">
        <div class="rank-number">ü•á</div>
        <div class="rank-info">
          <div class="username">{{ topPlayers.first?.username || '-' }}</div>
          <div class="score-info">
            <span class="score">{{ topPlayers.first?.score || 0 }}</span>
            <span class="time">{{ formatTime(topPlayers.first?.time || 0) }}</span>
          </div>
        </div>
      </div>
      
      <!-- Á¨¨‰∫åÂêç -->
      <div class="rank-item top-rank second-place">
        <div class="rank-number">ü•à</div>
        <div class="rank-info">
          <div class="username">{{ topPlayers.second?.username || '-' }}</div>
          <div class="score-info">
            <span class="score">{{ topPlayers.second?.score || 0 }}</span>
            <span class="time">{{ formatTime(topPlayers.second?.time || 0) }}</span>
          </div>
        </div>
      </div>
      
      <!-- Á¨¨‰∏âÂêç -->
      <div class="rank-item top-rank third-place">
        <div class="rank-number">ü•â</div>
        <div class="rank-info">
          <div class="username">{{ topPlayers.third?.username || '-' }}</div>
          <div class="score-info">
            <span class="score">{{ topPlayers.third?.score || 0 }}</span>
            <span class="time">{{ formatTime(topPlayers.third?.time || 0) }}</span>
          </div>
        </div>
      </div>
      
      <!-- ÂàÜÈöîÁ∫ø -->
      <div class="rank-separator">ÂÖ∂‰ªñÁé©ÂÆ∂</div>
      
      <!-- ÂÖ∂‰ªñÊéíÂêç -->
      <div 
        v-for="(player, index) in otherPlayers" 
        :key="index" 
        class="rank-item"
      >
        <div class="rank-number">{{ index + 4 }}</div>
        <div class="rank-info">
          <div class="username">{{ player.username }}</div>
          <div class="score-info">
            <span class="score">{{ player.score }}</span>
            <span class="time">{{ formatTime(player.time) }}</span>
          </div>
        </div>
      </div>
      
      <!-- Êó†ËÆ∞ÂΩïÊèêÁ§∫ -->
      <div v-if="otherPlayers.length === 0" class="no-records">
        ÊöÇÊó†ÂÖ∂‰ªñËÆ∞ÂΩï
      </div>
    </div>
    
    <!-- ÂΩìÂâçÁî®Êà∑ÊéíÂêç‰ø°ÊÅØ -->
    <div v-if="currentUserRank" class="current-user-rank">
      <p>ÊÇ®ÁöÑÊéíÂêç: #{{ currentUserRank.rank }}</p>
      <p>ÂæóÂàÜ: {{ currentUserRank.score }}</p>
      <p>Áî®Êó∂: {{ formatTime(currentUserRank.time) }}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RankList',
  props: {
    currentUser: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      rankData: []
    };
  },
  computed: {
    // ÊéíÂ∫èÁé©ÂÆ∂ÂàóË°®ÔºàÊåâÂæóÂàÜÈôçÂ∫èÔºåÂêåÂàÜÊåâÊó∂Èó¥ÂçáÂ∫èÔºâ
    sortedPlayers() {
      return [...this.rankData].sort((a, b) => {
        if (a.score !== b.score) {
          return b.score - a.score;
        }
        return a.time - b.time;
      });
    },
    // Ââç‰∏âÂêçÁé©ÂÆ∂Êï∞ÊçÆ
    topPlayers() {
      return {
        first: this.sortedPlayers[0] || null,
        second: this.sortedPlayers[1] || null,
        third: this.sortedPlayers[2] || null
      };
    },
    // Á¨¨ÂõõÂêçÂèä‰ª•ÂêéÁöÑÁé©ÂÆ∂Êï∞ÊçÆ
    otherPlayers() {
      return this.sortedPlayers.slice(3);
    },
    // ÂΩìÂâçÁî®Êà∑ÊéíÂêç‰ø°ÊÅØ
    currentUserRank() {
      if (!this.currentUser) return null;
      
      const rankIndex = this.sortedPlayers.findIndex(
        player => player.username === this.currentUser.username 
                && player.timestamp === this.currentUser.timestamp
      );
      
      if (rankIndex > -1) {
        const userData = this.sortedPlayers[rankIndex];
        return {
          rank: rankIndex + 1,
          score: userData.score,
          time: userData.time
        };
      }
      return null;
    }
  },
  mounted() {
    this.loadRankData();
  },
  methods: {
    // Âä†ËΩΩÊéíÂêçÊï∞ÊçÆ
    loadRankData() {
      const savedData = localStorage.getItem('treasureHuntRanks');
      if (savedData) {
        this.rankData = JSON.parse(savedData);
      } else {
        // Ê®°ÊãüÂàùÂßãÊï∞ÊçÆ
        this.rankData = [
          { 
            username: 'ÂØªÂÆùÂ§ßÂ∏à', 
            score: 1500, 
            time: 300, 
            timestamp: Date.now() - 86400000 
          },
          { 
            username: 'ÂÜíÈô©ÂÆ∂Â∞èÊòé', 
            score: 1200, 
            time: 350, 
            timestamp: Date.now() - 432000 
          }
        ];
      }
    },
    // Ê†ºÂºèÂåñÊó∂Èó¥ÔºàË°•ÂÖÖÂéü‰ª£Á†ÅÁº∫Â§±ÁöÑÊñπÊ≥ïÔºâ
    formatTime(seconds) {
      const mins = Math.floor(seconds / 60);
      const secs = seconds % 60;
      return `${mins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
    },
    // ËøîÂõûÊ∏∏ÊàèÔºàË°•ÂÖÖÂéü‰ª£Á†ÅÁº∫Â§±ÁöÑÊñπÊ≥ïÔºâ
    backToGame() {
      this.$router.push('/game'); // ÂÅáËÆæÊ∏∏Êàè‰∏ªÈ°µÈù¢Ë∑ØÁî±‰∏∫/game
    }
  }
};
</script>