<template>
  <div class="game-view">
    <div v-if="!isLoggedIn" class="login-container">
      <!-- ‰ΩøÁî®ÂÆåÊï¥ÁöÑUserLoginÁªÑ‰ª∂ -->
      <UserLogin @login="handleLogin" />
    </div>
    <div v-else-if="showPanorama" class="game-container">
      <!-- ÂÖ®ÊôØÂõæÂØºËà™ÁïåÈù¢ -->
      <div class="panorama-wrapper">
        <div class="panorama-header">
          <div class="player-info">
            <h3>{{ currentUser.username }}</h3>
            <p>ÂæóÂàÜ: {{ currentUser.score }} | ÂÆùËóè: {{ currentUser.treasures }}</p>
          </div>
          <div class="panorama-controls">
            <button @click="toggleAudio">
              {{ isAudioPlaying ? 'üîä ÈùôÈü≥' : 'üîà ÂºÄÂêØÈü≥Êïà' }}
            </button>
            <button @click="showRankList">üèÜ ÊéíË°åÊ¶ú</button>
          </div>
        </div>
        <Panorama 
          :scenes="gameScenes"
          :currentScene="currentScene"
          @sceneClick="handleSceneClick"
        />
      </div>
    </div>
    <div v-else class="game-container">
      <div class="game-header">
        <div class="player-info">
          <h3>{{ currentUser.username }}</h3>
          <p>ÂæóÂàÜ: {{ currentUser.score }} | ÂÆùËóè: {{ currentUser.treasures }}</p>
        </div>
        <div class="game-controls">
          <button @click="toggleAudio">
            {{ isAudioPlaying ? 'üîä ÈùôÈü≥' : 'üîà ÂºÄÂêØÈü≥Êïà' }}
          </button>
          <button @click="showRankList">üèÜ ÊéíË°åÊ¶ú</button>
          <button @click="backToPanorama">üó∫Ô∏è ÂÖ®ÊôØÂú∞Âõæ</button>
          <div class="scene-navigation">
            <button v-for="scene in availableScenes" :key="scene"
                    :class="{ active: currentScene === scene, locked: !isSceneUnlocked(scene) }"
                    @click="changeScene(scene)">
              {{ getSceneName(scene) }}
              <span v-if="!isSceneUnlocked(scene)" class="lock-icon">üîí</span>
              <span v-else-if="isSceneCompleted(scene)" class="completed-icon">üèÜ</span>
            </button>
          </div>
        </div>
      </div>
      <div class="game-scene">
        <LibraryScene v-if="currentScene === 'library'" 
                      @treasureFound="handleTreasureFound" 
                      @clueFound="handleFoundClue"/>
        <ForestScene v-if="currentScene === 'forest'" @treasureFound="handleTreasureFound"/>
        <RiverScene v-if="currentScene === 'river'" @treasureFound="handleTreasureFound"/>
        <TempleScene v-if="currentScene === 'temple'" @treasureFound="handleTreasureFound"/>
        <ChamberScene v-if="currentScene === 'chamber'" @treasureFound="handleTreasureFound"/>
      </div>
      <!-- ÁÆÄÂåñÁâàÊéíË°åÊ¶ú -->
      <div v-if="showRank" class="simple-rank">
        <div class="rank-header">
          <h2>ÊéíË°åÊ¶ú</h2>
          <button @click="showRank = false">ÂÖ≥Èó≠</button>
        </div>
        <div class="rank-list">
          <div v-for="(player, index) in rankedPlayers" :key="index" class="rank-item">
            <span class="rank-number">{{ index + 1 }}</span>
            <span class="rank-name">{{ player.username }}</span>
            <span class="rank-score">{{ player.score }}ÂàÜ</span>
          </div>
        </div>
      </div>
      <div class="clues-collection">
        <h4>Êî∂ÈõÜÁöÑÁ∫øÁ¥¢</h4>
        <div class="clues-list">
          <div v-for="(clue, index) in collectedClues" :key="index" class="clue-item">
            {{ getSceneName(clue.scene) }}:{{ clue.clue }}
          </div>
        </div>
      </div>
      <audio ref="audioPlayer" loop>
        <source src="/audio/game-background.mp3" type="audio/mpeg">
      </audio>
    </div>
  </div>
</template>

<script>
// ÂØºÂÖ•ÊâÄÊúâÂú∫ÊôØÁªÑ‰ª∂
import LibraryScene from '../components/game/LibraryScene.vue'
import ForestScene from '../components/game/ForestScene.vue'
import RiverScene from '../components/game/RiverScene.vue'
import ChamberScene from '../components/game/ChamberScene.vue'
import TempleScene from '../components/game/TempleScene.vue'
import Panorama from '../components/game/Panorama.vue'
import UserLogin from '../components/user/UserLogin.vue'

export default {
  name: 'GameView',
  components: {
    LibraryScene,
    ForestScene,
    RiverScene,
    ChamberScene,
    TempleScene,
    Panorama,
    UserLogin
  },
  data() {
    return {
      isLoggedIn: false,
      username: '',
      currentUser: {
        username: '',
        score: 0,
        treasures: 0,
        completedScenes: []
      },
      currentScene: 'library',
      showPanorama: true, // ÁôªÂΩïÂêéÈªòËÆ§ÊòæÁ§∫ÂÖ®ÊôØÂõæ
      collectedClues: [],
      showTreasureAlert: false,
      treasureAlertMessage: '',
      showRank: false,
      isAudioPlaying: false,
      players: [] // Â≠òÂÇ®ÊâÄÊúâÁé©ÂÆ∂Êï∞ÊçÆ
    };
  },
  computed: {
    rankedPlayers() {
      // Á°Æ‰øùÂΩìÂâçÁî®Êà∑Âú®ÊéíË°åÊ¶ú‰∏≠
      const allPlayers = [...this.players];
      const currentUserIndex = allPlayers.findIndex(p => p.username === this.currentUser.username);
      
      if (currentUserIndex === -1) {
        allPlayers.push({...this.currentUser});
      } else {
        allPlayers[currentUserIndex] = {...this.currentUser};
      }
      
      // ÊåâÂæóÂàÜÊéíÂ∫è
      return allPlayers.sort((a, b) => b.score - a.score);
    },
    // Ê†πÊçÆÂàÜÊï∞ËøîÂõûÂèØÁî®ÁöÑÂú∫ÊôØ
    availableScenes() {
      return ['library', 'forest', 'river', 'temple', 'chamber'];
    },
     gameScenes() {
      return [
        {
          id: 'library',
          name: 'Âè§ËÄÅÁöÑÂõæ‰π¶È¶Ü',
          imageUrl: '/images/scenes/Âè§ËÄÅÁöÑÂõæ‰π¶È¶Ü.png',
          unlocked: true,
          unlockScore: 0
        },
        {
          id: 'forest',
          name: 'Á•ûÁßòÊ£ÆÊûó',
          imageUrl: '/images/scenes/forest/forest.png',
          unlocked: this.isSceneUnlocked('forest'),
          unlockScore: 100
        },
        {
          id: 'river',
          name: 'Ê≤≥ÊµÅÊé¢Èô©',
          imageUrl: '/images/scenes/river/river.png',
          unlocked: this.isSceneUnlocked('river'),
          unlockScore: 300
        },
        {
          id: 'temple',
          name: 'Â§™Èò≥Á•ûÊÆø',
          imageUrl: '/images/scenes/Â§™Èò≥Á•ûÊÆø.png',
          unlocked: this.isSceneUnlocked('temple'),
          unlockScore: 500
        },
        {
          id: 'chamber',
          name: 'ÂÆùËóèÂØÜÂÆ§',
          imageUrl: '/images/scenes/ÂÆùËóèÂØÜÂÆ§.png',
          unlocked: this.isSceneUnlocked('chamber'),
          unlockScore: 800
        }
      ];
    }
  },
  methods: {
    // Ê∑ªÂä†Â§ÑÁêÜÂú∫ÊôØÁÇπÂáªÁöÑÊñπÊ≥ï
    handleSceneClick(sceneId) {
      this.changeScene(sceneId);
    },
    
    handleLogin(loginData) {
      this.isLoggedIn = true;
      this.currentUser.username = loginData.username;
      this.currentUser.isGuest = loginData.isGuest;
      // ÂàùÂßãÂåñÁî®Êà∑Êï∞ÊçÆ
      this.loadUserData();
      // ÁôªÂΩïÂêéÁõ¥Êé•ËøõÂÖ•ÂÖ®ÊôØÂõæÁïåÈù¢
      this.showPanorama = true;
    },
    
    loadUserData() {
      // ‰ªélocalStorageÂä†ËΩΩÁî®Êà∑Êï∞ÊçÆ
      const savedData = localStorage.getItem('treasureHuntData');
      if (savedData) {
        const data = JSON.parse(savedData);
        this.players = data.players || [];
        
        // Ê£ÄÊü•ÂΩìÂâçÁî®Êà∑ÊòØÂê¶Êúâ‰øùÂ≠òÁöÑÊï∞ÊçÆ
        const savedUser = this.players.find(p => p.username === this.currentUser.username);
        if (savedUser) {
          this.currentUser = {...savedUser};
        } else {
          // Á°Æ‰øùcompletedScenesÂ≠òÂú®
          this.currentUser.completedScenes = [];
        }
        
        // Âä†ËΩΩÂÖ∂‰ªñÊï∞ÊçÆ
        this.collectedClues = data.collectedClues || [];
      } else {
        // Á°Æ‰øùÊñ∞Áî®Êà∑ÊúâcompletedScenesÂ±ûÊÄß
        this.currentUser.completedScenes = [];
      }
    },
    
    saveUserData() {
      // ‰øùÂ≠òÊï∞ÊçÆÂà∞localStorage
      const data = {
        currentScene: this.currentScene,
        collectedClues: this.collectedClues,
        players: this.rankedPlayers
      };
      localStorage.setItem('treasureHuntData', JSON.stringify(data));
    },
    
    // Ê†πÊçÆÂàÜÊï∞Ê£ÄÊü•Âú∫ÊôØÊòØÂê¶Ëß£ÈîÅ
    isSceneUnlocked(scene) {
      // ‰∏¥Êó∂ÊµãËØïÊ®°ÂºèÔºöËÆæÁΩÆ‰∏∫trueÂèØ‰ª•Ëß£ÈîÅÊâÄÊúâÂú∫ÊôØ
      const testMode = false;
      if (testMode) return true;
      
      const scoreRequirements = {
        library: 0,
        forest: 100,
        river: 300,
        temple: 500,
        chamber: 800
      };
      
      return this.currentUser.score >= scoreRequirements[scene];
    },
    
    // Ê£ÄÊü•Âú∫ÊôØÊòØÂê¶Â∑≤ÂÆåÊàêÔºàÊâæÂà∞ÂÆùËóèÔºâ
    isSceneCompleted(scene) {
      return this.currentUser.completedScenes.includes(scene);
    },
    
    changeScene(sceneName) {
      // Ê£ÄÊü•Áî®Êà∑ÊòØÂê¶ÂèØ‰ª•ÂàáÊç¢Âà∞ËØ•Âú∫ÊôØ
      if (this.isSceneUnlocked(sceneName)) {
        this.currentScene = sceneName;
        this.showPanorama = false;
        this.saveUserData();
      } else {
        const scoreRequirements = {
          library: 0,
          forest: 100,
          river: 300,
          temple: 500,
          chamber: 800
        };
        alert(`ÈúÄË¶ÅËææÂà∞${scoreRequirements[sceneName]}ÂàÜÊâçËÉΩËß£ÈîÅ${this.getSceneName(sceneName)}ÔºÅ`);
      }
    },
    
    handleFoundClue(clue) {
      // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÊî∂ÈõÜ‰∫ÜËøô‰∏™Á∫øÁ¥¢
      const isAlreadyCollected = this.collectedClues.some(c => 
        c.scene === clue.scene && c.clue === clue.clue
      );
      
      if (!isAlreadyCollected) {
        this.collectedClues.push(clue);
        this.currentUser.score += 50; // ÊØè‰∏™Á∫øÁ¥¢50ÂàÜ
        this.saveUserData();
        
        // ÊòæÁ§∫Á∫øÁ¥¢ÂèëÁé∞ÊèêÁ§∫
        const TreasureAlert = this.$options.components.TreasureAlert;
        if (TreasureAlert) {
          this.$alert(`Ëé∑ÂæóÁ∫øÁ¥¢: ${this.getSceneName(clue.scene)}: ${clue.clue}`, 'ÂèëÁé∞Á∫øÁ¥¢', {
            confirmButtonText: 'Á°ÆÂÆö'
          });
        } else {
          alert(`Ëé∑ÂæóÁ∫øÁ¥¢: ${this.getSceneName(clue.scene)}: ${clue.clue}`);
        }
      }
    },
    
    toggleAudio() {
      if (this.isAudioPlaying) {
        if (this.$refs.audioPlayer) {
          this.$refs.audioPlayer.pause();
        }
      } else {
        if (this.$refs.audioPlayer) {
          this.$refs.audioPlayer.play();
        }
      }
      this.isAudioPlaying = !this.isAudioPlaying;
    },
    
    showRankList() {
      this.showRank = true;
    },
    
    handleTreasureFound(treasureData) {
      // Êõ¥Êñ∞Áî®Êà∑ÂàÜÊï∞ÂíåÂÆùËóèÊï∞Èáè
      this.currentUser.score += treasureData.score;
      this.currentUser.treasures += 1;
      
      // Ê†áËÆ∞Âú∫ÊôØ‰∏∫Â∑≤ÂÆåÊàê
      if (!this.currentUser.completedScenes.includes(treasureData.scene)) {
        this.currentUser.completedScenes.push(treasureData.scene);
      }
      
      // ÊòæÁ§∫ÊèêÁ§∫‰ø°ÊÅØ
      this.treasureAlertMessage = `ÊÅ≠Âñú‰Ω†Âú®${this.getSceneName(treasureData.scene)}ÊâæÂà∞ÂÆùËóèÔºåËé∑Âæó${treasureData.score}ÂàÜÔºÅ`;
      
      // ‰øùÂ≠òÊï∞ÊçÆ
      this.saveUserData();
      
      // Ê£ÄÊü•ÊòØÂê¶Ëß£ÈîÅ‰∫ÜÊñ∞Âú∫ÊôØ
      const newlyUnlocked = this.checkForNewUnlockedScenes();
      if (newlyUnlocked.length > 0) {
        const unlockedNames = newlyUnlocked.map(scene => this.getSceneName(scene)).join('„ÄÅ');
        alert(`${this.treasureAlertMessage}\nüéâ ÊÅ≠ÂñúËß£ÈîÅÊñ∞Âú∫ÊôØ: ${unlockedNames}`);
      } else {
        alert(this.treasureAlertMessage);
      }
    },
    
    // Ê£ÄÊü•ÊòØÂê¶ÊúâÊñ∞Ëß£ÈîÅÁöÑÂú∫ÊôØ
    checkForNewUnlockedScenes() {
      const newlyUnlocked = [];
      const allScenes = ['library', 'forest', 'river', 'temple', 'chamber'];
      
      // ÈÅçÂéÜÊâÄÊúâÂú∫ÊôØÔºåÊâæÂà∞ÂàöËß£ÈîÅÁöÑÂú∫ÊôØ
      for (const scene of allScenes) {
        if (scene !== 'library' && this.isSceneUnlocked(scene)) {
          // Ê£ÄÊü•ËØ•Âú∫ÊôØÊòØÂê¶ÊòØÂú®ËøôÊ¨°ÂÆùËóèÂèëÁé∞ÂêéÊâçËß£ÈîÅÁöÑ
          // ÈÄöËøáÊ£ÄÊü•ËØ•Âú∫ÊôØÊòØÂê¶Âú®Â∑≤ÂÆåÊàêÂú∫ÊôØ‰∏≠ÔºåÊàñËÄÖÁî®Êà∑ÂàÜÊï∞ÊòØÂê¶ÂàöÂàöËææÂà∞Ë¶ÅÊ±Ç
          const wasJustUnlocked = !this.currentUser.completedScenes.includes(scene);
          if (wasJustUnlocked) {
            newlyUnlocked.push(scene);
          }
        }
      }
      
      return newlyUnlocked;
    },
    
    getSceneName(scene) {
      const sceneNames = {
        library: 'Âè§ËÄÅÁöÑÂõæ‰π¶È¶Ü',
        forest: 'Á•ûÁßòÊ£ÆÊûó',
        river: 'Ê≤≥ÊµÅÊé¢Èô©',
        temple: 'Â§™Èò≥Á•ûÊÆø',
        chamber: 'ÂÆùËóèÂØÜÂÆ§'
      };
      return sceneNames[scene || this.currentScene] || (scene || this.currentScene);
    },
    
    // ÂÖ®ÊôØÂõæÁÉ≠ÁÇπÁÇπÂáªÂ§ÑÁêÜ
    handleHotspotClick(hotspot) {
      if (hotspot.unlocked || hotspot.target === 'library') {
        this.changeScene(hotspot.target);
      } else {
        const scoreRequirements = {
          library: 0,
          forest: 100,
          river: 300,
          temple: 500,
          chamber: 800
        };
        alert(`ÈúÄË¶ÅËææÂà∞${scoreRequirements[hotspot.target]}ÂàÜÊâçËÉΩËß£ÈîÅ${hotspot.label}ÔºÅ`);
      }
    },
    
    // ËøîÂõûÂÖ®ÊôØÂõæÁïåÈù¢
    backToPanorama() {
      this.showPanorama = true;
    },
    
    switchToNextScene() {
      const scenes = ['library', 'forest', 'river', 'temple', 'chamber'];
      const currentIndex = scenes.indexOf(this.currentScene);
      
      // Êü•Êâæ‰∏ã‰∏Ä‰∏™Â∑≤Ëß£ÈîÅÁöÑÂú∫ÊôØ
      for (let i = 1; i < scenes.length; i++) {
        const nextIndex = (currentIndex + i) % scenes.length;
        if (this.isSceneUnlocked(scenes[nextIndex])) {
          this.changeScene(scenes[nextIndex]);
          return;
        }
      }
      
      // Â¶ÇÊûúÊ≤°ÊúâÊâæÂà∞‰∏ã‰∏Ä‰∏™Â∑≤Ëß£ÈîÅÁöÑÂú∫ÊôØÔºå‰øùÊåÅÂΩìÂâçÂú∫ÊôØ
    }
  },
  beforeUnmount() {
    // ÁªÑ‰ª∂Âç∏ËΩΩÂâç‰øùÂ≠òÊï∞ÊçÆ
    this.saveUserData();
  }
}
</script>

<style scoped>
.game-view {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.panorama-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
}

.panorama-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(10px);
  z-index: 100;
}

.panorama-controls {
  display: flex;
  gap: 10px;
}

.panorama-controls button {
  padding: 8px 16px;
  font-size: 14px;
  border: none;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.panorama-controls button:hover {
  background-color: rgba(255, 255, 255, 0.2);
  transform: translateY(-2px);
}

.lock-icon {
  margin-left: 5px;
  opacity: 0.5;
}

.completed-icon {
  margin-left: 5px;
  color: #ffd700;
}

/* ÂÖ∂‰ªñÊ†∑Âºè‰øùÊåÅ‰∏çÂèò */
</style>