<template>
  <div class="library-scene" @click="handleClick">
    <div class="scene-background" :style="{ backgroundImage: `url(${backgroundImage})` }"></div>
    <div class="scene-content">
      <h2>Âè§ËÄÅÁöÑÂõæ‰π¶È¶Ü</h2>
      <p class="scene-description">Êª°ÊòØÁÅ∞Â∞òÁöÑ‰π¶Êû∂‰∏äÊëÜÊª°‰∫ÜÂè§ËÄÅÁöÑ‰π¶Á±ç„ÄÇÁ©∫Ê∞î‰∏≠Âº•Êº´ÁùÄÁ∫∏Âº†ÂíåÂ¢®Ê∞¥ÁöÑÂë≥ÈÅì„ÄÇ‰∏ÄÊú¨‰π¶‰ºº‰πéÂú®ÂæÆÂæÆÂèëÂÖâ...</p>
      
      <!-- ‰π¶Êû∂ -->
      <div class="bookshelves">
        <div v-for="shelf in bookshelves" :key="shelf.id" class="bookshelf" 
             :style="{ left: shelf.x + '%', top: shelf.y + '%', height: shelf.height + 'px' }">
          <div v-for="book in shelf.books" :key="book.id" class="book" 
               :class="{ 'highlighted': book.highlighted, 'discovered': book.discovered }" 
               @click.stop="examineBook(book, shelf, $event)">
            <span class="book-spine"></span>
          </div>
        </div>
      </div>
      
      <!-- ÂèëÂÖâÁöÑÁ•ûÁßò‰π¶Á±ç -->
      <div v-if="showMysteryBook" class="mystery-book" @click.stop="openMysteryBook">
        <div class="book-icon">üìú</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LibraryScene',
  data() {
    return {
      backgroundImage: '/images/scenes/Âè§ËÄÅÁöÑÂõæ‰π¶È¶Ü.png',
      bookshelves: [
        {
          id: 1,
          x: 15,
          y: 30,
          height: 400,
          books: Array.from({ length: 12 }, (_, i) => ({
            id: `1-${i}`,
            highlighted: i === 5, // ÈöèÊú∫ÈÄâÊã©‰∏ÄÊú¨‰Ωú‰∏∫Á∫øÁ¥¢
            discovered: false
          }))
        },
        {
          id: 2,
          x: 40,
          y: 40,
          height: 300,
          books: Array.from({ length: 8 }, (_, i) => ({
            id: `2-${i}`,
            highlighted: i === 2,
            discovered: false
          }))
        },
        {
          id: 3,
          x: 70,
          y: 35,
          height: 350,
          books: Array.from({ length: 10 }, (_, i) => ({
            id: `3-${i}`,
            highlighted: i === 7,
            discovered: false
          }))
        }
      ],
      showMysteryBook: false,
      discoveredClues: 0
    }
  },
  methods: {
    handleClick(event) {
      // Âü∫Á°ÄÁÇπÂáª‰∫§‰∫í
    },
    
    examineBook(book, shelf, event) {
      if (book.highlighted && !book.discovered) {
        book.discovered = true;
        this.discoveredClues++;
        this.checkForMysteryBook();
        
        // Ê∑ªÂä†Á∫øÁ¥¢Âà∞Áà∂ÁªÑ‰ª∂
        this.$emit('clueFound', {
          scene: 'library',
          clue: `‰ªé‰π¶Êû∂${shelf.id}ÊâæÂà∞ÁöÑÂè§ËÄÅÁ¨îËÆ∞`
        });
        
        // Ê∑ªÂä†‰∏Ä‰∫õËßÜËßâÂèçÈ¶à
        const bookEl = event.currentTarget;
        bookEl.style.transform = 'scale(1.1) rotate(5deg)';
        setTimeout(() => {
          bookEl.style.transform = '';
        }, 500);
      }
    },
    
    checkForMysteryBook() {
      // ÁªüËÆ°ÊâÄÊúâÂ∑≤ÂèëÁé∞ÁöÑÁ∫øÁ¥¢
      const totalClues = this.bookshelves.reduce((sum, shelf) => {
        return sum + shelf.books.filter(book => book.highlighted).length;
      }, 0);
      
      // Â¶ÇÊûúÊâÄÊúâÁ∫øÁ¥¢ÈÉΩÂ∑≤ÂèëÁé∞ÔºåÊòæÁ§∫Á•ûÁßò‰π¶Á±ç
      if (this.discoveredClues === totalClues) {
        setTimeout(() => {
          this.showMysteryBook = true;
        }, 1000);
      }
    },
    
    openMysteryBook() {
      this.$emit('treasureFound', {
        score: 220,
        scene: 'library',
        timeBonus: 30
      });
    }
  }
}
</script>

<style scoped>
.library-scene {
  width: 100%;
  height: 100vh;
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.scene-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
  filter: brightness(0.6);
  transition: filter 0.3s ease;
}

.library-scene:hover .scene-background {
  filter: brightness(0.7);
}

.scene-content {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 40px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.scene-content h2 {
  font-size: 3rem;
  margin-bottom: 20px;
  color: #d4af37;
  text-align: center;
  animation: fadeIn 1s ease-in-out;
}

.scene-description {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto 30px;
  text-align: center;
  line-height: 1.6;
  animation: fadeIn 1.5s ease-in-out;
}

.bookshelves {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.bookshelf {
  position: absolute;
  width: 60px;
  background: rgba(139, 69, 19, 0.8);
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 10px 5px;
  pointer-events: auto;
}

.book {
  width: 100%;
  height: 15px;
  cursor: pointer;
  transition: transform 0.3s ease;
  position: relative;
}

.book-spine {
  display: block;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #6b4226, #8b5a2b);
  border-radius: 2px;
  transition: all 0.3s ease;
}

.book.highlighted .book-spine {
  background: linear-gradient(90deg, #ffd700, #ffed4e);
  animation: glow 2s infinite alternate;
}

.book.discovered .book-spine {
  background: linear-gradient(90deg, #7cfc00, #98fb98);
  animation: none;
}

.mystery-book {
  position: absolute;
  bottom: 30%;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  animation: float 3s ease-in-out infinite, glow 2s infinite alternate;
}

.book-icon {
  font-size: 64px;
}

/* Âä®ÁîªÂÆö‰πâ */
@keyframes glow {
  from {
    box-shadow: 0 0 5px rgba(255, 215, 0, 0.5);
  }
  to {
    box-shadow: 0 0 20px rgba(255, 215, 0, 0.9);
  }
}

@keyframes float {
  0%, 20%, 50%, 80%, 100% {
    transform: translateX(-50%) translateY(0);
  }
  40% {
    transform: translateX(-50%) translateY(-20px);
  }
  60% {
    transform: translateX(-50%) translateY(-10px);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
</style>